.nft-roadmap {
    position: relative;
    min-height: 150px;
    margin-top: 10rem;
}

.nft-roadmap__line {
    position: absolute;
    z-index: 2;
    left: 0;
    width: 4px;
    top: -50px;
    bottom: -50px;
    background-color: #ffb74d;
    display: none;
}

.nft-roadmap__line::before,
.nft-roadmap__line::after {
    position: absolute;
    display: block;
    content: '';
    height: 1rem;
    width: 1rem;
    border-radius: 50%;
    background-color: #ffb74d;
    left: 50%;
    transform: translateX(-50%);
}

.nft-roadmap__line::after {
    bottom: 0;
}

.nft-roadmap__section {
    display: flex;
    opacity: 0.2;
    transition: 600ms ease;
    position: relative;
    z-index: 1;
    margin: 50px 0;
    padding: 1rem;
    border-radius: 1rem;
    align-items: center;
    min-height: 300px;
}

.nft-roadmap__head {
    display: block;
    height: 1rem;
    width: 1rem;
    position: absolute;
    border-radius: 50%;
    background-color: #ffb74d;
    left: 0;
    top: 10%;
    transform: translateX(-35%);
}

.nft-roadmap__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 0.5rem;
}

.nft-roadmap__img-content {
    width: 60%;
}

.nft-roadmap__img-content img {
    width: 100%;
    height: auto;
}

.nft-roadmap__show-me:nth-child(n) {
    opacity: 1;
}

.nft-roadmap__section:nth-child(even) .nft-roadmap__img-content {
    margin-left: 0;
}

.nft-roadmap__section:nth-child(odd) .nft-roadmap__img-content {
    margin-right: 0;
    margin-left: 0;
}

.nft-roadmap__text-content h2 {
    text-transform: uppercase;
}

.sm-image {
    width: 250px !important;
}

@media(min-width: 768px) {
    .nft-roadmap__content {
        flex-direction: row;
    }
    .nft-roadmap__line,
    .nft-roadmap__head {
        left: calc(50%);
    }
    .nft-roadmap__head {
        top: 20%;
    }
    .nft-roadmap__text-content {
        width: calc(50% - 2rem);
    }
    .nft-roadmap__img-content {
        width: 40%;
    }
    .nft-roadmap__img-content img {
        max-width: 350px;
    }
    .nft-roadmap__section:nth-child(odd) .nft-roadmap__content {
        flex-direction: row-reverse;
    }
    .nft-roadmap__section:nth-child(even) .nft-roadmap__img-content {
        margin-left: 5rem;
    }
    .nft-roadmap__section:nth-child(odd) .nft-roadmap__img-content {
        margin-right: 5rem;
    }
    .nft-roadmap__text-content {
        width: calc(45% - 2rem);
    }
}